import React from 'react'
import { Link } from 'react-router'
var Navigation = React.createClass({
	render: function() {
    return (
    	<div className="nav">
			<div className="services">
				<ul>
					<li>
				        <Link to="/statistics">
					        <i className="iconfont icon-ai" data-toggle="tooltip"  title="模型展示" data-placement="bottom"/>
				        </Link>
		          	</li>
				    <li>
				        <Link to="/help">
				          	<i className="iconfont icon-bi" data-toggle="tooltip"  title="统计数据" data-placement="bottom"/>
				        </Link>
				     </li>
		          	<li>
		          		<Link to="/messagequery">
		          			<i className="iconfont iconfont" data-toggle="tooltip"  title="查询消息" data-placement="bottom">&#xe66f;</i>
		          		</Link>
		          	</li>
				</ul>
			</div>
		</div>
    );
  }
});
export default Navigation;


